import http from "@assets/http";
import { serviceName } from "@assets/common";
// 注册服务商认证
interface newAgencyParams {
  companyName: string;
  legalPerson: string;
  address: string;
  addressAll: string;
  businessLicenseImgurl: string;
  businessLicenseNumber: string;
  idcardImgurl: string;
}
export const newAgencyApi = (params: newAgencyParams) => {
  return http.put(`${serviceName.agentSystem}/user/newAgency`, params);
};
