<template>
  <div class="certification main-container">
    <!-- 未认证 -->
    <div class="certification-not" v-if="paramsData.checkStatus === 0">
      <h6 class="title">认证服务商</h6>
      <el-form
        ref="ruleFormRef"
        :model="formData"
        :rules="rules"
        label-width="115px"
        class="ruleForm"
      >
        <el-form-item label="公司名称" prop="companyName">
          <el-select
            v-model="formData.companyId"
            filterable
            remote
            reserve-keyword
            placeholder="请输入"
            @change="handleSelectCompany"
            :remote-method="handleRemoteCompany"
            :loading="paramsData.remoteLoading"
          >
            <el-option
              v-for="item in paramsData.companyList"
              :key="item.id"
              :label="item.companyName"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="法人姓名" prop="legalPerson">
          <el-input v-model="formData.legalPerson" disabled></el-input>
        </el-form-item>
        <el-form-item label="地址" prop="addressAll">
          <el-input v-model="formData.addressAll" disabled></el-input>
        </el-form-item>
        <el-form-item label="营业号码" prop="businessLicenseNumber">
          <el-input
            v-model="formData.businessLicenseNumber"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item label="上传营业执照" prop="businessLicenseImgurl">
          <CustomUpload
            @emitUpload="changeBusinessUploadImg"
            listType="picture-card"
          >
            <template v-slot:example>
              <div class="example-info">
                <p class="accept">支持.jpg、.jpeg、.png格式，只能上传一张</p>
                <p
                  class="look-button"
                  @click="lookExample('营业执照示例', 'businessLicense')"
                >
                  <span>查看示例</span>
                  <el-icon><arrow-right /></el-icon>
                </p>
              </div>
            </template>
          </CustomUpload>
        </el-form-item>
        <el-form-item label="上传身份证" prop="idcardImgurl">
          <CustomUpload
            @emitUpload="changeIdCardUploadImg"
            listType="picture-card"
          >
            <template v-slot:example>
              <div class="example-info">
                <p class="accept">支持.jpg、.jpeg、.png格式，只能上传一张</p>
                <p
                  class="look-button"
                  @click="lookExample('身份证示例', 'idCard')"
                >
                  <span>查看示例</span>
                  <el-icon><arrow-right /></el-icon>
                </p>
              </div>
            </template>
          </CustomUpload>
        </el-form-item>
        <el-form-item class="submit-item">
          <el-button type="primary" @click="submitForm()">提交</el-button>
        </el-form-item>
      </el-form>
    </div>
    <!--   认证审核中  -->
    <div class="certification-review" v-if="paramsData.checkStatus === 1">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-certification-waiting"></use>
      </svg>
      <p class="word">信息审核中</p>
      <p class="prompt">
        尊敬的服务商，您好！您的信息正在审核中，1~3个工作日将会审核完成，请您耐心等待！
      </p>
    </div>
    <!--  认证审核驳回  -->
    <div class="certification-refuse" v-if="paramsData.checkStatus === 3">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-certification-refuse"></use>
      </svg>
      <p class="word">审核驳回</p>
      <p class="prompt">驳回原因：{{ paramsData.checkAdvice }}</p>
      <el-button type="primary" @click="paramsData.checkStatus = 0"
        >重新认证
      </el-button>
    </div>
  </div>
  <!--  查看示例弹框  -->
  <el-dialog
    class="custom-dialog"
    width="700px"
    lock-scroll
    append-to-body
    v-model="paramsData.exampleVisible"
    :title="paramsData.exampleTitle"
  >
    <div class="img-box">
      <img :src="paramsData.exampleImgUrl" alt="" />
    </div>
  </el-dialog>
</template>
<script lang="ts" setup>
import { ref, shallowReactive, onMounted } from "vue";
import { ArrowRight } from "@element-plus/icons-vue";

import { useVueRouter } from "@assets/useHook";
import { LocalStorage } from "jz-tool-lib";

import { newAgencyApi } from "@api/certification-service-api";
import { searchBizCompanyApi, searchBizCompanyInfoApi } from "@api/common-api";

defineOptions({
  name: "certificationService",
});

const ruleFormRef = ref();
const { routeQuery } = useVueRouter();
const paramsData = shallowReactive({
  submitFlag: false,
  remoteLoading: false,
  exampleVisible: false,
  exampleTitle: "",
  exampleImgUrl: "",
  checkStatus: -1,
  checkAdvice: LocalStorage.getItem("bd_checkAdvice"),
  companyList: [],
});
const formData = shallowReactive({
  companyId: "",
  companyName: "",
  legalPerson: "",
  addressAll: "",
  idcardImgurl: "",
  businessLicenseNumber: "",
  businessLicenseImgurl: "",
});

const rules = {
  companyName: [
    {
      required: true,
      message: "请输入公司名称",
      trigger: "blur",
    },
  ],
  businessLicenseNumber: [
    {
      required: true,
      message: "营业号码不能为空",
      trigger: "blur",
    },
  ],
};

// 查询Biz公司
const handleRemoteCompany = async (query: string) => {
  if (query) {
    paramsData.remoteLoading = true;
    const { data } = await searchBizCompanyApi.getData(query);
    paramsData.remoteLoading = false;
    (paramsData.companyList as any[]) =
      searchBizCompanyApi.handleData(data).list;
  } else {
    paramsData.companyList = [];
  }
};
// 选择公司
const handleSelectCompany = (value) => {
  getCompanyInfo(value);
  const companyInfo: any = paramsData.companyList.find(
    (item: Record<string, any>) => item.id === value
  );
  formData.companyName = companyInfo?.companyName;
};
// 获取公司信息
const getCompanyInfo = async (companyId) => {
  try {
    const { data } = await searchBizCompanyInfoApi.getData(companyId);
    const companyInfo = searchBizCompanyInfoApi.handleData(data);
    formData.addressAll = companyInfo.address;
    formData.legalPerson = companyInfo.juridicalPerson;
    formData.businessLicenseNumber = companyInfo.creditNumber;
  } catch (e) {
    console.log(e);
  }
};

// 上传营业执照图片回调
const changeBusinessUploadImg = (value) => {
  formData.businessLicenseImgurl = value;
};

// 上传身份证图片回调
const changeIdCardUploadImg = (value) => {
  formData.idcardImgurl = value;
};

// 查看图片
const lookExample = (title, imgName) => {
  paramsData.exampleTitle = title;
  paramsData.exampleImgUrl = require(`../../assets/images/${imgName}.png`);
  paramsData.exampleVisible = true;
};

// 提交认证
const submitForm = () => {
  ruleFormRef.value.validate(async (valid) => {
    if (valid) {
      if (paramsData.submitFlag) return;
      paramsData.submitFlag = true;
      const params = {
        companyName: formData.companyName,
        legalPerson: formData.legalPerson,
        address: formData.addressAll,
        addressAll: formData.addressAll,
        idcardImgurl: formData.idcardImgurl.toString(),
        businessLicenseNumber: formData.businessLicenseNumber,
        businessLicenseImgurl: formData.businessLicenseImgurl.toString(),
      };
      try {
        await newAgencyApi(params);
        paramsData.checkStatus = 1;
        LocalStorage.setItem("bd_checkStatus", 1);
      } finally {
        paramsData.submitFlag = false;
      }
    } else {
      return false;
    }
  });
};

onMounted(() => {
  if (routeQuery.value.checkStatus) {
    paramsData.checkStatus = Number(routeQuery.value.checkStatus);
  } else {
    paramsData.checkStatus = LocalStorage.getItem("bd_checkStatus");
  }
});
</script>
<style lang="scss" scoped>
.certification-not {
  max-width: 1200px;
  min-width: 980px;
  margin: 0 auto;
  background: #ffffff;
  border-radius: 8px;

  .title {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 0 40px;
    height: 100px;
    font-size: 24px;
    font-weight: 500;
    color: #242934;
    line-height: 24px;
    border-bottom: 1px solid #edf1f6;
  }

  .ruleForm {
    width: 650px;
    padding: 40px;

    :deep(.el-form-item__label) {
      padding: 0 20px 0 0;
      color: #242934;
    }

    .submit-item.el-form-item {
      margin-top: 40px;
    }
    .el-select {
      width: 100%;
    }
  }

  .example-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 10px 0;

    p {
      font-size: 14px;
      line-height: 1;
    }

    .accept {
      color: #9ca6b9;
      margin-bottom: 10px;
    }

    .look-button {
      display: flex;
      align-items: center;
      color: #0056ff;
      cursor: pointer;
      line-height: 1;
      font-size: 14px;
    }
  }
}
.certification-review,
.certification-refuse {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 1200px;
  height: 524px;
  margin: 0 auto;
  background-color: #ffffff;
  svg {
    width: 68px;
    height: 68px;
  }
  .word {
    font-size: 24px;
    color: #242934;
    line-height: 20px;
    margin: 28px 0 20px;
  }
  .prompt {
    font-size: 14px;
    color: #9ca6b9;
    line-height: 1;
    text-align: center;
  }
  .el-button {
    margin-top: 36px;
  }
}
.custom-dialog {
  .img-box {
    padding: 20px 0 40px;
    img {
      display: block;
      margin: 0 auto;
    }
  }
}
</style>
